<script>
import { mapActions } from "vuex"
import helpers from "@/plugins/helper.js"
export default {
  name: "Login",

  data: () => ({
    error: "",
    step: 1,
    isFormValid: false,
    isForgetFormValid: false,
    isNewPasswordValid: false,
    token: "",
    memberId: "",
    newPassword: "222222",
    showPass: true,
    loginData: {
      email: "",
      password: ""
    },
    inputRules: [
      (v) => !!v || "Bu alan gerekli",
      (v) => (v && v.length >= 6) || "En Az 6 Karakter Giriniz"
    ],
    emailRules: [
      (v) => !!v || "Bu alan gerekli",
      (v) =>
        !v ||
        /^\w+([.-]?\w+)*@\w+([.-]?\w+)*(\.\w{2,3})+$/.test(v) ||
        "E-mail geersiz"
    ],
    registerData: {
      name: "",
      password: "",
      email: "",
      role: "ye",
      published: true
    }
  }),
  computed: {
    isLoggedin() {
      return this.$store.state.user.isLoggedin
    }
  },
  watch: {
    isLoggedin(newValue) {
      if (newValue) {
        this.$router.push("/dashboard")
      }
    },
    error(newValue) {
      if (newValue) {
        setTimeout(() => {
          this.error = false
        }, 2000)
      }
    }
  },
  methods: {
    ...helpers,
    ...mapActions(["login", "register", "forgot", "check"]),

    handleSingIn() {
      this.login({
        parent: "collections",
        child: "members",
        data: {
          fields: { password: 0 },
          filter: {
            type: "login",
            email: this.loginData.email,
            password: this.loginData.password.trim()
          }
        }
      })
    },
    async handleForgot() {
      this.registerData.token = "123456" //this.generateCode(6)
      let result = await this.forgot({
        data: {
          fields: { email: 1, name: 1 },
          filter: {
            type: "forgot",
            email: this.registerData.email,
            token: this.registerData.token
          }
        }
      })
      if (result) {
        this.step = 1
        this.registerData.email = ""
      }
    },
    async handleSingUp() {
      let result = await this.register(this.registerData)
      result ? (this.step = 1) : ""
    },
    async handleUpdate() {
      let token = await this.check({
        filter: {
          token: this.token,
          _id: this.memberId
        }
      })
      if (token) {
        let pass = await this.register({
          password: this.newPassword,
          _id: this.memberId,
          token: ""
        })
        pass ? (this.step = 1) : console.log("ifre kaydedilemedi")
      } else {
        console.log("token bulunamad")
      }
    }
  },
  created() {
    if (this.isLoggedin) {
      this.$router.push({ name: "Dashboard" })
    }
  },
  mounted() {
    if (this.$route.query.token && this.$route.query.token.length > 8) {
      var arr = this.$route.query.token.split("-")
      this.token = arr[0]
      this.memberId = arr[1]
      this.step = 4
    }
  }
}
</script>
